
import { SET_PATH_URL   }  from "./constants/action-types";



    const initialState = {
         lastPathUrl         : '',
  }



    function rootReducer(state = initialState, action) {

    
      switch(action.type) {
        case SET_PATH_URL: 
           return Object.assign({}, state, {
            lastPathUrl: action.payload.lastPathUrl
        });
        break;

      }

      return state;
    }



  export default rootReducer;